*{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
body{
  padding: 60px;
}
input{
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin: 10px auto;
  overflow: hidden;
  animation-duration: 2s;
}
.card h4{
  padding: 5px;
  text-align: center;
}
.card img{
  display: block;
  margin: auto;
  max-height: 170px;
}